.header {
  &_div {
    display: flex;
    align-items: center;
    padding: 0px 30px;
    border-bottom: solid 1px black;
  }
  &__logo {
    height: 110px;
  }
  &__icon {
    width: 30px;
    height: 30px;
  }
  &__img {
    display: flex;
    justify-content: end;
    flex-grow: 1;
  }
}
